
    import { defineComponent, onMounted, ref, watch} from "vue";

    export default defineComponent({
        name: 'SurveyText',
        emits: ['onSurveyTextInput'],
        props: {
            id: String,
            question: String,
            placeholder: String,
            mandatory: Boolean,
            rows: Number,
            disabled: Boolean
        },
        setup(props, context) {
            // define data and watchers
            const value = ref('');
            watch(value, (curValue, preValue) => {
                context.emit('onSurveyTextInput', {
                    id: props.id,
                    value: curValue
                });
            });

            // return data
            return {
                value
            }
        }
    })
